textarea {
    width: 96%;
    max-width: 100%;
    resize: vertical;
    background-color: #0D192E;
    border: 2px solid #0D192E;
    color: white;
    border-radius: 2px;
    font-family: 'Inter', sans-serif;
    outline: none;
    transition: border 0.1s;
    padding: 0.5rem;
    line-height: 2;
    white-space: pre-line;
    min-height: 96vh;
}

textarea:focus {
    border: 2px solid #009F97;
    transition: border 0.1s;
    outline: none;
}
