html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
  background-color: #1c2e4e;
  color: white;
  letter-spacing: 0.5px;
}

header {
  background-color: #1b1f30;
  /* background-color: #3bf8af; */
  font-family: "Open Sans", sans-serif;
  padding-top: 2.5rem;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
}

.goto_help {
  height: 1em;
}

.goto_converter {
  height: 1em;
}

.title_box {
  margin: auto 0;
  padding-bottom: 1rem;
  width: 50vw;
}

.venti {
  width: 40vw;
  min-width: 30vw;
  align-self: flex-end;
}

h1 {
  padding: 1rem 0;
  font-size: 1.5em;
  text-align: center;
}

.converter {
  display: flex;
  max-width: 80vw;
  margin: 1rem auto 2rem;
}

.low {
  color: #E84258;
}

.medium {
  color: #b4e7c7;
}

.high {
  color: #FEE191;
}

@media (max-width: 865px) {
  .venti {
    display: none;
  }

  .title_box {
    width: 80vw;
  }

  .converter {
    display: block;
  }
}
