ul {
  list-style-type: none;
  background-color: #1c2e4e;
  margin: 0;
  padding: 1rem 3rem;
  width: 100vw;
  position: fixed;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 7px 29px 0px;
  align-items: flex-start;
  z-index: 2;
}

li {
  display: inline-block;
  margin: 0 1rem;
  
  align-self: flex-start;
}

li a {
  color: white;
  
}

i {
  margin-right: 0.5em;
}

.mobile_menu_container {
  display: none;
}

@media (max-width: 865px) {
  ul {
    padding: 0 auto;
  }
}

@media (max-width: 520px) {
  ul {
    display: none;
  }

  .mobile_menu_container {
    display: block;
    position: fixed;
  }

  .hamburger_menu {
    padding: 1rem 1.5rem;
    width: 100vw;
    background-color: #1c2e4e;
  }

  .mobile_item_container {
    padding: 1rem 1.5rem;
    width: 50%;
    height: 100vh;
    background-color: #1c2e4e;
  }

  #remove_display {
    display: none;
  }

  .mobile_item {
    margin-bottom: 2rem;
  }

  .mobile_item a {
    color: white;
  }
}
