.help {
  width: 80vw;
  margin: 1rem auto;
}

.notes {
  margin-bottom: 3rem;
}

.ps_key {
  background-color: #009f97;
  margin-right: 1rem;
  padding: 0.2rem 0.5rem;
  border-radius: 5px;
}

.lol {
  font-size: 0.6rem;
}

h3 {
  font-size: 0.9em;
}

h4 {
  font-size: 0.8em;
}

h3,
h4 {
  margin-bottom: 0.5rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
}

a {
  margin-top: 0;
  font-size: 0.9rem;
}

.low {
  color: #e84258;
}

.medium {
  color: #b4e7c7;
}

.high {
  color: #fee191;
}

@media (max-width: 865px) {
  .help {
    line-height: 1.6;
  }
  span {
    line-height: 2;
  }
}
