.box {
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
}

.box_header {
    display: flex;
    padding: 0 1rem;
    background-color: #0D192E;
    border-radius: 10px 10px 0 0;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
}

.colored {
    background-color: #009F97;
}

select {
    margin: 0.8rem 0;
    padding: 0.2rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
    font-size: 1rem;
    border:none;
    background-color: white;
    border-radius: 4px;
    
}

select:focus {
    outline: none;
}

select:hover {
    cursor: pointer;
}

option {
    margin-top: 2rem;
    height: 1rem;
    background-color: white;
    font-family: 'Inter', sans-serif;
}

/* music icon */
.box_header img {
    height: 1em;
    margin-left: 0.5em;
    align-self: center;
}

.help_icon {
    align-self: center;
    color: black;
    margin-left: auto;
    font-size: 1.2em;
}

.setting_icon {
    cursor: pointer;
    align-self: center;
    font-size: 1.2em;
}

.setting_icon:hover {
    color: #9D159F;
}

.setting_icon_transform {
    color: #E11A3E;
    transform: rotate(20deg);
}

h2 {
    font-size: 1.1rem;
}

.box_content {
    padding: 0.5rem 1rem 1rem;
    background-color: #0D192E;
    border: 1px solid #0D192E;
    border-radius: 0 0 10px 10px;
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    color: #E5E5E5;
}