.converter_notes {
    width: 50vw;
    min-width: 48%;
    margin-left: 0.5rem;
    word-wrap: break-word;
    white-space: pre-line;
}

.converter_box {
    word-wrap: break-word;
    height: 100vh;
    line-height: 1.8;
    overflow-y: auto;
}

.placeholder {
    margin: 0;
    color: #8b8b8b;
    user-select: none;
}

.more_option {
    display: none;
    background-color: #0D192E;
    border: 2px solid #009F97;
    border-radius: 5px;
    padding:0.5rem;
    margin-bottom: 0.5rem;
    font-size: 0.95rem;
}

.more_option_display {
    display: block;
}

input[type='checkbox'] {
    margin-right: 0.5rem;
    width: 0.95rem;
    outline: none;
}

label {
    user-select: none;;
}

@media (max-width: 865px) {
    .converter_notes {
        width: 80vw;
    }
}